.map-page {
  display: flex;
  flex-direction: column;
  width: 100%;
  border-radius: 20px;
  background: #fff;
  overflow: hidden;
  box-shadow: 0 0 60px rgba(71, 85, 105, 0.08);
}

/* Tabs */
.map-tabs {
  display: flex;
  align-items: center;
  gap: 16px;
  padding: 16px 24px;
  background: #fff;
}

.map-tab {
  background: #f3f4f6;
  border: none;
  padding: 8px 18px;
  border-radius: 20px;
  color: #374151;
  font-weight: 500;
  cursor: pointer;
  transition: 0.25s ease;

  &.active {
    background: #e5edff;
    color: #3b5ef0;
  }

  &:hover {
    background: #edf1f9;
  }
}

/* Content layout */
.map-content {
  position: relative;
  display: flex;
  flex-direction: column;
  height: calc(100vh - 180px);

  &.split {
    flex-direction: row;
  }
}

/* Map area */
.map-container {
  flex: 1;
  height: 100%;
  border-radius: 0 20px 20px 0;
}

/* Stays list */
.stays-list {
  width: 45%;
  min-width: 380px;
  max-width: 480px;
  overflow-y: auto;
  padding: 20px 24px;
  border-right: 1px solid #e5e9f0;
  background: #fff;

  .stay-card {
    display: flex;
    flex-direction: column;
    margin-bottom: 20px;
    border-radius: 16px;
    overflow: hidden;
    border: 1px solid #e5e9f0;
    box-shadow: 0 2px 8px rgba(0, 0, 0, 0.05);
    transition: transform 0.2s ease;

    &:hover {
      transform: translateY(-2px);
    }

    .stay-image {
      width: 100%;
      height: 180px;
      object-fit: cover;
    }

    .stay-info {
      padding: 14px 16px;

      .stay-location {
        color: #64748b;
        font-size: 14px;
        font-weight: 500;
      }

      .stay-name {
        color: #1e293b;
        font-size: 18px;
        font-weight: 600;
        margin: 4px 0;
      }

      .stay-address {
        color: #475569;
        font-size: 14px;
        margin-bottom: 8px;
      }

      .stay-meta {
        display: flex;
        gap: 10px;
        align-items: center;
        font-size: 13px;
        color: #475569;

        .sponsored {
          margin-left: auto;
          font-size: 12px;
          color: #3b5ef0;
          background: #eef3ff;
          padding: 2px 8px;
          border-radius: 8px;
        }
      }
    }
  }
}

/* Map markers */
.map-marker {
  width: 60px;
  height: 60px;
  border-radius: 50%;
  overflow: hidden;
  border: 3px solid white;
  box-shadow: 0 0 8px rgba(0, 0, 0, 0.15);
  position: relative;

  img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    border-radius: 50%;
  }

  .marker-info {
    position: absolute;
    bottom: -18px;
    left: 50%;
    transform: translateX(-50%);
    background: rgba(255, 255, 255, 0.95);
    border-radius: 20px;
    padding: 2px 8px;
    font-size: 12px;
    color: #1e293b;
    font-weight: 500;
    box-shadow: 0 0 4px rgba(0, 0, 0, 0.1);
  }
}

.pin {
  width: 12px;
  height: 12px;
  background: #e53935;
  border-radius: 50%;
  border: 2px solid #fff;
  box-shadow: 0 0 6px rgba(0, 0, 0, 0.25);
}
